import { useState } from 'react';
import { useForm } from 'react-hook-form';
import { useDropzone } from 'react-dropzone';
import PropTypes from 'prop-types';
import Button from '../../UI/Button/Button';
import './PlacesForm.css';

const PlacesForm = ({ onClose, isPlacePopupOpened, showInputs }) => {
  const {
    register,
    formState: { errors },
    handleSubmit,
    reset,
  } = useForm();

  const [image, setImage] = useState([]);
  const { getInputProps } = useDropzone({
    accept: 'image/*',
    onDrop: (acceptedFiles) => {
      setImage(
        acceptedFiles.map((file) =>
          Object.assign(file, {
            url: URL.createObjectURL(file),
          })
        )
      );
    },
  });

  const onSubmit = (placeData) => {
    const extendedPlaceData = {
      ...placeData,
      imageUrl: image[0].url,
      chosen: true,
    };

    console.log(extendedPlaceData);
    // placeData ‚Äî —Ç–æ, —á—Ç–æ —Å–æ–±–µ—Ä—ë—Ç—Å—è —Å —Ñ–æ—Ä–º—ã;
    // extendedPlaceData ‚Äî –¥–æ–±–∞–≤–ª–µ–Ω –∞–¥—Ä–µ—Å –∫–∞—Ä—Ç–∏–Ω–∫–∏ –∏ –∞—Ç—Ä–∏–±—É—Ç,
    // —É–∫–∞–∑—ã–≤–∞—é—â–∏–π, —á—Ç–æ —ç—Ç–æ "–≤—ã–±–æ—Ä –Ω–∞—Å—Ç–∞–≤–Ω–∏–∫–∞"; –≤–æ—Ç —ç—Ç–æ –∏ –ø–æ–π–¥—ë—Ç –Ω–∞ —Å–µ—Ä–≤–µ—Ä
    // (–º–± chosen —Å–∞–º–æ –∫–∞–∫-—Ç–æ –±—ç–∫–µ–Ω–¥–æ–º –¥–µ–ª–∞–µ—Ç—Å—è, —Ç–∞–º –µ—â—ë id –≤—Ä–æ–¥–µ –±—É–¥–µ—Ç)
    reset();
    setImage([]);
    onClose();
  };

  return (
    // <Popup popupType="popup_type_recommendation" onClose={onClose}>
    <form
      className={`popup__form popup__form_type_recommendation ${
        isPlacePopupOpened ? 'popup__form_opened' : ''
      }`}
      name="recommendation"
      onSubmit={handleSubmit(onSubmit)}
    >
      <div className={`popup__inputs ${showInputs ? 'popup__inputs_show' : ''}`}>
        <input
          {...register('title', {
            required: '–ù–∞–∑–≤–∞–Ω–∏–µ*',
            minLength: { value: 2, message: '–ù–∞–∑–≤–∞–Ω–∏–µ –º–µ—Å—Ç–∞ –¥–æ–ª–∂–Ω–æ –±—ã—Ç—å –Ω–µ –º–µ–Ω–µ–µ 2 —Å–∏–º–≤–æ–ª–æ–≤' },
          })}
          type="text"
          placeholder={errors.title ? errors.title.message : '–ù–∞–∑–≤–∞–Ω–∏–µ*'}
          className={`popup__input popup__input_type_name ${
            errors.title ? 'popup__input-error' : ''
          }`}
        />
        <input
          {...register('link')}
          type="url"
          className={`popup__input popup__input_type_url ${
            errors.link ? 'popup__input-error' : ''
          }`}
          placeholder={errors.link ? errors.link.message : '–°–∞–π—Ç'}
        />
        <input
          {...register('address', { required: '–ê–¥—Ä–µ—Å*' })}
          type="text"
          className={`popup__input popup__input_type_address ${
            errors.address ? 'popup__input-error' : ''
          }`}
          placeholder={errors.address ? errors.address.message : '–ê–¥—Ä–µ—Å*'}
        />
        <div className="popup__input popup__input_type_boy">
          <label htmlFor="boy" className={errors.sex ? 'popup__radio-error' : 'popup__radio'}>
            <input
              className={`custom-radio ${errors.sex ? 'custom-radio-error' : ''}`}
              type="radio"
              value="–ú–∞–ª—å—á–∏–∫"
              {...register('sex', { required: '–ú–∞–ª—å—á–∏–∫' })}
              id="boy"
            />
            –ú–∞–ª—å—á–∏–∫
          </label>
        </div>
        <div className="popup__input popup__input_type_girl">
          <label htmlFor="girl" className={errors.sex ? 'popup__radio-error' : 'popup__radio'}>
            <input
              className={`custom-radio ${errors.sex ? 'custom-radio-error' : ''}`}
              type="radio"
              value="–î–µ–≤–æ—á–∫–∞"
              {...register('sex', { required: '–î–µ–≤–æ—á–∫–∞' })}
              id="girl"
            />
            –î–µ–≤–æ—á–∫–∞
          </label>
        </div>
        <input
          {...register('age', { required: '–í–æ–∑—Ä–∞—Å—Ç*' })}
          type="number"
          min="8"
          className={`popup__input popup__input_type_age ${errors.age ? 'popup__input-error' : ''}`}
          placeholder={errors.age ? errors.age.message : '–í–æ–∑—Ä–∞—Å—Ç*'}
        />
        <select
          {...register('type', { required: '–¢–∏–ø –æ—Ç–¥—ã—Ö–∞*' })}
          className={`popup__select ${
            errors.type ? 'popup__select-error' : ''
          } popup__select_type_relax`}
        >
          <option value="">–¢–∏–ø –æ—Ç–¥—ã—Ö–∞*</option>
          <option value="–ê–∫—Ç–∏–≤–Ω—ã–π">–ê–∫—Ç–∏–≤–Ω—ã–π</option>
          <option value="–†–∞–∑–≤–ª–µ–∫–∞—Ç–µ–ª—å–Ω—ã–π">–†–∞–∑–≤–ª–µ–∫–∞—Ç–µ–ª—å–Ω—ã–π</option>
          <option value="–ü–æ–∑–Ω–∞–≤–∞—Ç–µ–ª—å–Ω—ã–π">–ü–æ–∑–Ω–∞–≤–∞—Ç–µ–ª—å–Ω—ã–π</option>
        </select>
        <textarea
          {...register('description', {
            required: '–ö–æ–º–º–µ–Ω—Ç–∞—Ä–∏–π* –ü–æ–¥–µ–ª–∏—Ç–µ—Å—å –≤–ø–µ—á–∞—Ç–ª–µ–Ω–∏—è–º–∏ –æ –ø—Ä–æ–≤–µ–¥—ë–Ω–Ω–æ–º –≤—Ä–µ–º–µ–Ω–∏',
          })}
          className={`popup__textarea popup__textarea_type_description ${
            errors.description ? 'popup__textarea-error' : ''
          }`}
          placeholder={errors.description ? errors.description.message : '–ö–æ–º–º–µ–Ω—Ç–∞—Ä–∏–π*'}
        />
        <div className="popup__feedback">
          <label
            htmlFor="addImageBtn"
            className={image ? 'popup__feedback-text' : 'popup__feedback-text-error '}
          >
            <input {...getInputProps()} id="addImageBtn" className="popup__feedback-button" />
            –î–æ–±–∞–≤–∏—Ç—å —Ñ–æ—Ç–æ
          </label>
        </div>
        <div className="popup__submit">
          <Button
            className="button button_color_darkgray popup__submit-btn"
            type="submit"
            disabled={
              errors.title ||
              errors.description ||
              errors.address ||
              errors.sex ||
              errors.age ||
              errors.type
                ? 'disabled'
                : null
            }
          >
            –û—Ç–ø—Ä–∞–≤–∏—Ç—å
          </Button>
        </div>
      </div>
    </form>
    // </Popup>
  );
};

PlacesForm.propTypes = {
  onClose: PropTypes.func,
  isPlacePopupOpened: PropTypes.bool.isRequired,
  showInputs: PropTypes.bool.isRequired,
};

PlacesForm.defaultProps = {
  onClose: () => {},
};

export default PlacesForm;
