import { useRef } from 'react';
import './StoryPage.css';
import Button from '../../UI/Button/Button';
import StoryCard from '../../Cards/StoryCard/StoryCard';
import fullStories from '../../../utils/stories';
import Heading from '../../UI/Heading/Heading';

const StoryPage = () => {
  // stotyRefs —Å–≤—è–∂–µ—Ç id –∫–Ω–æ–ø–æ–∫ —Å –∏–º–µ–Ω–∞–º–∏, —Å article —Å–æ–æ—Ç–≤–µ—Ç—Å—Ç–≤—É—é—â–∏—Ö –∏—Å—Ç–æ—Ä–∏–π
  const storyRefs = useRef([]);
  function handleScrollToStory(event) {
    storyRefs.current[event.target.id].scrollIntoView();
  }

  return (
    <section className="storypage content main__section">
      <Heading>–ò—Å—Ç–æ—Ä–∏–∏ –¥—Ä—É–∂–±—ã</Heading>
      <p className="storypage__caption">
        –†–µ–∑—É–ª—å—Ç–∞—Ç –Ω–∞—à–µ–π —Ä–∞–±–æ—Ç—ã —Å–ª–æ–∂–Ω–æ –ø–æ–∫–∞–∑–∞—Ç—å —Ü–∏—Ñ—Ä–∞–º–∏. –ö–∞–∫ –∏–∑–º–µ—Ä–∏—Ç—å –∏&nbsp;–≤–∑–≤–µ—Å–∏—Ç—å –¥—Ä—É–∂–±—É? –ö–∞–∫
        –æ—Ü–µ–Ω–∏—Ç—å —É—Å–ø–µ—Ö–∏ –∞–±—Å–æ–ª—é—Ç–Ω–æ —Ä–∞–∑–Ω—ã—Ö –¥–µ—Ç–µ–π? –£&nbsp;–∫–∞–∂–¥–æ–π –ø–∞—Ä—ã&nbsp;&mdash; —Å–≤–æ–π —Ä–µ–∑—É–ª—å—Ç–∞—Ç
        –∏&nbsp;—Å–≤–æ–∏ –¥–æ—Å—Ç–∏–∂–µ–Ω–∏—è. –ò–º–µ–Ω–Ω–æ –æ–±&nbsp;—ç—Ç–æ–º –º—ã&nbsp;—Ä–∞—Å—Å–∫–∞–∑—ã–≤–∞–µ–º –≤&nbsp;–∏—Å—Ç–æ—Ä–∏—è—Ö.
      </p>
      <div className="scroll-container">
        <div className="buttons-scroll">
          {fullStories.map((fullStory) => (
            <Button
              id={fullStory.id}
              key={fullStory.id}
              className="button button_color_black button_place_scroll"
              type="button"
              onClick={handleScrollToStory}
            >
              {fullStory.heading}
            </Button>
          ))}
        </div>
      </div>
      {fullStories.map((fullStory) => (
        <StoryCard
          storyRef={(el) => {
            storyRefs.current[fullStory.id] = el;
          }}
          key={fullStory.id}
          fullStory={fullStory}
          isStoryPage
        />
      ))}
    </section>
  );
};

export default StoryPage;
