import { useEffect, useState, useContext } from 'react';
import PropTypes from 'prop-types';
import ReactPaginate from 'react-paginate';
import CurrentUserContext from '../../../contexts/CurrentUserContext';
import Api from '../../../utils/api';
import Button from '../../UI/Button/Button';
import MainVideoCard from '../../Cards/MainVideoCard/MainVideoCard';
import VideoCard from '../../Cards/VideoCard/VideoCard';
import './VideosPage.css';
import Heading from '../../UI/Heading/Heading';

const VideosPage = ({ handleVideoClick }) => {
  const currentUser = useContext(CurrentUserContext);
  const [videosData, setVideosData] = useState([]);
  const [chosenVideo, setChosenVideo] = useState({});
  const [videoTags, setVideoTags] = useState([]);
  const [activeTag, setActiveTag] = useState('–í—Å–µ');
  const [currentPage, setCurrentPage] = useState(0);
  const perPage = 16;
  const offset = currentPage * perPage;
  let pageCount;

  useEffect(() => {
    Api.getVideos()
      .then((data) => {
        setVideosData(data);
        // –í–∏–¥–µ–æ, –≤—ã–±—Ä–∞–Ω–Ω–æ–µ –º–æ–¥–µ—Ä–∞—Ç–æ—Ä–æ–º
        const chosen = data.filter((video) => video.chosen)[0];
        setChosenVideo({ ...chosen });
        // –°–ø–∏—Å–æ–∫ —É–Ω–∏–∫–∞–ª—å–Ω—ã—Ö —Ç–µ–≥–æ–≤ –¥–ª—è –∫–Ω–æ–ø–æ–∫ —Ñ–∏–ª—å—Ç—Ä–∞-—Ä—É–±—Ä–∏–∫–∞—Ç–æ—Ä–∞
        const tagsData = data
          .map((video) => video.tag.name)
          .filter((item, i, arr) => arr.indexOf(item) === i);
        setVideoTags(['–í—Å–µ', ...tagsData]);
        pageCount = Math.ceil(data.length / perPage);
      })
      .catch(console.log);
  }, []);

  const currentPageData = videosData
    .slice(offset, offset + perPage)
    .filter((video) => activeTag === '–í—Å–µ' || activeTag === video.tag.name)
    // –ù–µ–∞–≤—Ç–æ—Ä–∏–∑–æ–≤–∞–Ω–Ω—ã–π –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –Ω–µ –≤–∏–¥–∏—Ç –≤–∏–¥–µ–æ —Å —Ç–µ–≥–æ–º "–†–µ—Å—É—Ä—Å–Ω–∞—è –≥—Ä—É–ø–ø–∞"
    .filter(({ tag }) => currentUser || !currentUser === (tag.name !== '–†–µ—Å—É—Ä—Å–Ω–∞—è –≥—Ä—É–ø–ø–∞'))
    .map(({ tag, id: key, ...args }) => (
      <VideoCard type="video" key={key} handleVideoClick={handleVideoClick} tag={[tag]} {...args} />
    ));

  const handlePageClick = ({ selected }) => {
    setCurrentPage(selected);
  };

  const handleTagFilter = (tag) => {
    if (activeTag && activeTag === tag) {
      setActiveTag('–í—Å–µ');
    } else {
      setActiveTag(tag);
    }
  };

  return (
    <section className="videopage content main__section">
      <Heading>–í–∏–¥–µ–æ</Heading>
      <div className="scroll-container">
        <div className="buttons-scroll">
          {videoTags.map((tag) =>
            // –ù–µ–∞–≤—Ç–æ—Ä–∏–∑–æ–≤–∞–Ω–Ω—ã–π –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –Ω–µ –≤–∏–¥–∏—Ç –≤–∏–¥–µ–æ —Å —Ç–µ–≥–æ–º "–†–µ—Å—É—Ä—Å–Ω–∞—è –≥—Ä—É–ø–ø–∞"
            !currentUser && tag === '–†–µ—Å—É—Ä—Å–Ω–∞—è –≥—Ä—É–ø–ø–∞' ? null : (
              <Button
                className={`button button_color_black button_place_scroll ${
                  tag === activeTag ? 'button_color_black_active' : ''
                }`}
                type="button"
                key={tag}
                onClick={() => handleTagFilter(tag)}
              >
                {tag}
              </Button>
            )
          )}
        </div>
      </div>
      {activeTag === '–í—Å–µ' && (
        <section className="mainvideo videopage__bigvideo">
          <MainVideoCard video={chosenVideo} />
        </section>
      )}
      <ul className="videopage__list">{currentPageData}</ul>
      <ReactPaginate
        previousLabel=""
        nextLabel=""
        pageCount={pageCount}
        marginPagesDisplayed={1}
        pageRangeDisplayed={5}
        containerClassName="pagination"
        breakClassName="pagination__link"
        breakLabel="..."
        activeClassName="pagination__link_active"
        nextClassName="pagination__forward"
        pageLinkClassName="pagination__link"
        onPageChange={handlePageClick}
      />
    </section>
  );
};

VideosPage.propTypes = {
  handleVideoClick: PropTypes.func,
};

VideosPage.defaultProps = {
  handleVideoClick: () => {},
};

export default VideosPage;
