import PropTypes from 'prop-types';
import { useForm } from 'react-hook-form';
import { Link } from 'react-router-dom';
import Button from '../../UI/Button/Button';
import Popup from '../Popup/Popup';
import './authPopup.css';

const AuthPopup = ({ closeAuthModal, submitModal }) => {
  //  –ü–æ–¥–∫–ª—é—á–∏–ª –±–∏–±–ª–∏–æ—Ç–µ–∫—É–∫ —Ä–µ–∞–∫—Ç-—Ñ–æ—Ä–º.     register, handleSubmit –∏ errors - —ç—Ç–æ –≤—Å–µ –æ—Ç—Ç—É–¥–∞
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();

  const onSubmit = ({ login, password }) => {
    submitModal(login, password);
  };
  return (
    <Popup popupType="popup_type_login" onClose={closeAuthModal}>
      <div className="login">
        <Button
          type="button"
          className="popup__button-close popup__button-close_place_login"
          onClick={closeAuthModal}
        />
        <h3 className="login__title">–í—Ö–æ–¥</h3>
        <div className="login__texts">
          <p className="login__text">
            –í—Ö–æ–¥ –≤ –ª–∏—á–Ω—ã–π –∫–∞–±–∏–Ω–µ—Ç –¥–æ—Å—Ç—É–ø–µ–Ω –Ω–∞—Å—Ç–∞–≤–Ω–∏–∫–∞–º –ø—Ä–æ–≥—Ä–∞–º–º—ã ¬´–°—Ç–∞—Ä—à–∏–µ –ë—Ä–∞—Ç—å—è –°—Ç–∞—Ä—à–∏–µ –°—ë—Å—Ç—Ä—ã¬ª.
          </p>
          <p className="login__text">
            –ü–æ–∂–∞–ª—É–π—Å—Ç–∞, –≤–≤–µ–¥–∏—Ç–µ –ª–æ–≥–∏–Ω –∏ –ø–∞—Ä–æ–ª—å –∏–∑&nbsp;–ø–∏—Å—å–º–∞. –ï—Å–ª–∏ –≤–∞–º –Ω–µ –ø—Ä–∏—Ö–æ–¥–∏–ª–æ –ø–∏—Å—å–º–æ,
            —Å–≤—è–∂–∏—Ç–µ—Å—å —Å&nbsp;–≤–∞—à–∏–º –∫—É—Ä–∞—Ç–æ—Ä–æ–º.
          </p>
        </div>

        <form
          className="popup__form popup__form_type_login"
          name="login"
          onSubmit={handleSubmit(onSubmit)}
        >
          <input
            {...register('login', {
              required: '–õ–æ–≥–∏–Ω*',
            })}
            type="text"
            placeholder={errors.login ? errors.login.message : '–õ–æ–≥–∏–Ω'}
            id="login"
            className={`popup__input popup__input_type_login ${
              errors.login ? 'popup__input-error' : ''
            }`}
          />
          <input
            {...register('password', {
              required: '–ü–∞—Ä–æ–ª—å*',
            })}
            type="password"
            placeholder={errors.password ? errors.password.message : '–ü–∞—Ä–æ–ª—å'}
            id="password"
            className={`popup__input popup__input_type_password ${
              errors.password ? 'popup__input-error' : ''
            }`}
          />

          <Link className="login__link" to="/">
            –ó–∞–±—ã–ª–∏ –ø–∞—Ä–æ–ª—å?
          </Link>
          <Button
            type="submit"
            className="button popup__submit-btn popup__submit-btn_type_login"
            disabled={errors.login || errors.password ? 'disabled' : null}
          >
            –í–æ–π—Ç–∏
          </Button>
        </form>
      </div>
    </Popup>
  );
};

AuthPopup.propTypes = {
  closeAuthModal: PropTypes.func.isRequired,
  submitModal: PropTypes.func.isRequired,
};
AuthPopup.defaultProps = {};
export default AuthPopup;
