import { useEffect, useState } from 'react';
import ReactPaginate from 'react-paginate';
import Api from '../../../utils/api';
import BookCard from '../../Cards/BookCard/BookCard';
import Button from '../../UI/Button/Button';
import Heading from '../../UI/Heading/Heading';
import './BooksPage.css';

const BooksPage = () => {
  const [booksData, setBooksData] = useState([]);
  const [genres, setGenres] = useState([]);
  const [activeGenre, setActiveGenre] = useState('–í—Å–µ');
  const [currentPage, setCurrentPage] = useState(0);
  const perPage = 16;
  const offset = currentPage * perPage;
  let pageCount;

  useEffect(() => {
    Api.getBooks()
      .then((data) => {
        const scienceBooks = data.filter((book) => book.genre === '–ù–∞—É—á–Ω—ã–µ');
        const fictionBooks = data.filter((book) => book.genre === '–•—É–¥–æ–∂–µ—Å—Ç–≤–µ–Ω–Ω—ã–µ');
        // –ø–æ —Ñ–∏–ª—å—Ç—Ä—É "–í—Å–µ" –∫–Ω–∏–≥–∏ –≤—ã—Ö–æ–¥—è—Ç –≤ –ø–æ—Ä—è–¥–∫–µ –Ω–∞—É—á–Ω–∞—è - —Ö—É–¥–æ–∂–µ—Å—Ç–≤–µ–Ω–Ω–∞—è - –Ω–∞—É—á - —Ö—É–¥ –∏ —Ç.–¥.
        const orderedBookList = scienceBooks
          .map((sb, i) => [sb, fictionBooks[i]])
          .flat()
          .filter((book) => book);
        setBooksData(orderedBookList);
        const genresData = data
          .map((item) => item.genre)
          .filter((item, i, arr) => arr.indexOf(item) === i);
        setGenres(['–í—Å–µ', ...genresData]);
        pageCount = Math.ceil(data.length / perPage);
      })
      .catch((err) => console.log(`–û—à–∏–±–∫–∞: ${err}`));
  }, []);

  const currentPageData = booksData
    .slice(offset, offset + perPage)
    .filter((book) => activeGenre === '–í—Å–µ' || activeGenre === book.genre)
    .map(({ title, author, year, description, genre, id }) => (
      <BookCard
        title={title}
        author={author}
        year={year}
        description={description}
        genre={genre}
        key={id}
      />
    ));

  const handlePageClick = ({ selected }) => {
    setCurrentPage(selected);
  };

  const handleGenreFilter = (genre) => {
    if (activeGenre && activeGenre === genre) {
      setActiveGenre('–í—Å–µ');
    } else {
      setActiveGenre(genre);
    }
  };

  return (
    <section className="bookpage content main__section">
      <Heading>–ö–Ω–∏–≥–∏</Heading>
      <div className="scroll-container">
        <div className="buttons-scroll">
          {genres.map((genre) => (
            <Button
              className={`button button_color_black button_place_scroll ${
                genre === activeGenre ? 'button_color_black_active' : ''
              }`}
              type="button"
              key={genre}
              onClick={() => handleGenreFilter(genre)}
            >
              {genre}
            </Button>
          ))}
        </div>
      </div>
      <ul className="bookpage__list">{currentPageData}</ul>
      <ReactPaginate
        previousLabel=""
        nextLabel=""
        pageCount={pageCount}
        marginPagesDisplayed={0}
        pageRangeDisplayed={5}
        containerClassName="pagination"
        breakClassName="pagination__link"
        breakLabel="..."
        activeClassName="pagination__link_active"
        nextClassName="pagination__forward"
        pageLinkClassName="pagination__link"
        onPageChange={handlePageClick}
      />
    </section>
  );
};

export default BooksPage;
