import { useEffect, useState } from 'react';
import ReactPaginate from 'react-paginate';
import Api from '../../../utils/api';
import Button from '../../UI/Button/Button';
import VideoCard from '../../Cards/VideoCard/VideoCard';
import './MoviesPage.css';
import Heading from '../../UI/Heading/Heading';

const MoviesPage = () => {
  const [moviesData, setMoviesData] = useState([]);
  const [genres, setGenres] = useState([]);
  const [activeGenre, setActiveGenre] = useState('–í—Å–µ');
  const [currentPage, setCurrentPage] = useState(0);
  const perPage = 16;
  const offset = currentPage * perPage;
  let pageCount;

  useEffect(() => {
    Api.getMovies()
      .then((data) => {
        setMoviesData(data);
        // –°–ø–∏—Å–æ–∫ —É–Ω–∏–∫–∞–ª—å–Ω—ã—Ö –∂–∞–Ω—Ä–æ–≤ –¥–ª—è –∫–Ω–æ–ø–æ–∫ —Ñ–∏–ª—å—Ç—Ä–∞-—Ä—É–±—Ä–∏–∫–∞—Ç–æ—Ä–∞
        const genresData = data
          .map((movie) => movie.tags)
          .flat()
          .map((tag) => tag.name)
          .filter((item, i, arr) => arr.indexOf(item) === i);
        setGenres(['–í—Å–µ', ...genresData]);
        // –ü–æ–¥—Å—á—ë—Ç –∫–æ–ª-–≤–∞ —Å—Ç—Ä–∞–Ω–∏—Ü: –æ–∫—Ä—É–≥–ª–µ–Ω–∏–µ —á–∞—Å—Ç–Ω–æ–≥–æ –æ–±—â–µ–≥–æ –∫–æ–ª-–≤–∞ –Ω–∞ –∫–æ–ª-–≤–æ –Ω–∞ –æ–¥–Ω–æ–π —Å—Ç—Ä–∞–Ω–∏—Ü–µ
        pageCount = Math.ceil(data.length / perPage);
      })
      .catch((err) => console.log(`–û—à–∏–±–∫–∞: ${err}`));
  }, []);

  const currentPageData = moviesData
    .slice(offset, offset + perPage)
    .filter((movie) => {
      const tagTexts = movie.tags.map((tag) => tag.name);
      return activeGenre === '–í—Å–µ' || tagTexts.includes(activeGenre);
    })
    .map(({ id: key, ...args }) => <VideoCard key={key} type="movie" {...args} />);

  const handlePageClick = ({ selected }) => {
    setCurrentPage(selected);
  };

  const handleGenreFilter = (genre) => {
    if (activeGenre && activeGenre === genre) {
      setActiveGenre('–í—Å–µ');
    } else {
      setActiveGenre(genre);
    }
  };

  return (
    <section className="filmpage content main__section">
      <Heading>–§–∏–ª—å–º—ã</Heading>
      <div className="scroll-container">
        <div className="buttons-scroll">
          {genres.map((genre) => (
            <Button
              className={`button button_color_black button_place_scroll ${
                genre === activeGenre ? 'button_color_black_active' : ''
              }`}
              type="button"
              key={genre}
              onClick={() => handleGenreFilter(genre)}
            >
              {genre}
            </Button>
          ))}
        </div>
      </div>
      <ul className="filmpage__list">{currentPageData}</ul>
      <ReactPaginate
        previousLabel=""
        nextLabel=""
        pageCount={pageCount}
        marginPagesDisplayed={1}
        pageRangeDisplayed={5}
        containerClassName="pagination"
        breakClassName="pagination__link"
        breakLabel="..."
        activeClassName="pagination__link_active"
        nextClassName="pagination__forward"
        pageLinkClassName="pagination__link"
        onPageChange={handlePageClick}
      />
    </section>
  );
};

export default MoviesPage;
